/* eslint-disable no-use-before-define */
/* eslint-disable no-loop-func */
import game from "../classes/game";
import boardRenderer from "./boardRenderer";

let length;
let orientation = "horizontal";
let validPlacement = true;
let playerTurn = 1;

const renderBoard = () => {
    const boardElement = document.querySelector(".game-board");
    boardElement.innerHTML = "";
    const { board } = playerTurn === 1 ? game.player1.gameBoard : game.player2.gameBoard;

    for(let i = 0; i < board.length; i+=1) {
        const newRow = document.createElement("div");
        newRow.className = "grid-row";
        for(let j = 0; j < board.length; j+=1) {
            const newTile = document.createElement("div");
            const sign = board[i][j];
            if(sign === "s") {
                newTile.className = "selected";
            } else {
                newTile.setAttribute("value", `${j}${i}`)
                newTile.addEventListener("dragover", (e) => {
                    e.preventDefault();
                    changeTiles(newTile, true);
                })
                newTile.addEventListener("dragleave", (e) => {
                    e.preventDefault();
                    changeTiles(newTile, false);
                })
                newTile.addEventListener("drop", () => {
                    if(validPlacement) {
                        let [x, y] = newTile.getAttribute("value").split("");
                        x = Number(x);
                        y = Number(y);
                        if(orientation === "horizontal") {
                            game.placeShip(length, "right", x, y);
                        } else {
                            game.placeShip(length, "down", x, y);
                        }
                    }
                    renderBoard();
                })
            } 
            newTile.classList.add("player-tile");           
            newRow.appendChild(newTile);
        }
        boardElement.appendChild(newRow);
    }
}

const changeTiles = (startTile, highlight) => {
    const tiles = [startTile];
    const [x, y] = startTile.getAttribute("value").split("");
    validPlacement = true;
    for(let i = 0; i < length; i += 1) {
        const newX = orientation === "horizontal" ? Number(x) + i : Number(x);
        const newY = orientation === "vertical" ? Number(y) + i : Number(y);
        const tile = document.querySelector(`div[value="${newX}${newY}"]`);
        if(tile === null) {
            validPlacement = false;
            break;
        } else {
            tiles.push(tile);
        }
    }

    const highlightClass = validPlacement ? "selected" : "invalid-selected";
    if(highlight) {
        tiles.forEach(tile => tile.classList.add(highlightClass));
    } else {
        tiles.forEach(tile => tile.classList.remove(highlightClass));
    }
    
}

const mouseInsideGameBoard = (e) => {
    const board = document.querySelector(".game-board").getBoundingClientRect();
    if(e.clientX >= board.left && e.clientX <= board.right  && e.clientY >= board.top && e.clientY <= board.bottom) {
        return true;
    }
    return false;
}

const piecesContainer = () => {
    const container = document.createElement("div");
    container.className = "placeable-pieces-container";   

    for(let i = 4; i >= 1; i -= 1) { // Row
        const row = document.createElement("div");
        row.className = "row";
        for(let j = 4; j >= i; j -= 1) { // Piece
            const piece = document.createElement("div");
            piece.setAttribute("value", i);
            piece.setAttribute("draggable", true);
            piece.setAttribute("data-placed", false);
            piece.className = "piece";
            for(let k = i; k > 0; k -= 1) { // Square
                const tile = document.createElement("div");
                tile.className = "player-tile";
                tile.style.width = "3vh";
                piece.appendChild(tile);
            }
            piece.addEventListener("dragstart", (e) => {
                length = i;
                piece.classList.add("dragging");
                // Make drag image disappear
                const empty = document.createElement("span");
                empty.setAttribute("style", "display: none");
                e.dataTransfer.setDragImage(empty, 0, 0);
            })
            piece.addEventListener("dragend", (e) => {
                if(mouseInsideGameBoard(e) && validPlacement) {
                    piece.setAttribute("draggable", false);
                    piece.setAttribute("data-placed", true);
                } else {
                    piece.classList.remove("dragging");
                }
            })
            row.appendChild(piece);
        }
        container.appendChild(row);
    }
    return container;
}

const rotateButton = () => {
    const button = document.createElement("button");
    button.innerHTML = "Rotate";
    button.classList.add("rotate-button", "game-button");
    button.addEventListener("click", () => {
        orientation = orientation === "horizontal" ? "vertical" : "horizontal";
        const pieces = document.querySelectorAll(".piece");
        pieces.forEach(piece => {
            if(orientation === "horizontal") {
                piece.classList.remove("piece-vertical");
                piece.setAttribute("style", "flex-direction: row");
                const rows = document.querySelectorAll(".row");
                [...rows].forEach(row => {
                    row.setAttribute("style", "flex-direction: row");
                })
                const container = document.querySelector(".placeable-pieces-container");
                container.setAttribute("style", "flex-direction: column")
            } else {
                piece.classList.add("piece-vertical");
                const numTiles = piece.getAttribute("value");
                const height = `${numTiles * 3}vh`;
                piece.setAttribute("style", `height: ${height}`);
                const rows = document.querySelectorAll(".row");
                [...rows].forEach(row => {
                    row.setAttribute("style", "flex-direction: column");
                })
                const container = document.querySelector(".placeable-pieces-container");
                container.setAttribute("style", "flex-direction: row")
            }
        })
    })
    return button;
}

const allPiecesPlaced = () => {
    const pieces = [...document.querySelectorAll(".piece")];
    const len = pieces.length;
    for(let i = 0; i < len; i+= 1) {
        if(pieces[i].dataset.placed === "false") {
            const error = document.querySelector(".error");
            error.textContent = "All pieces must be used";
            return false;
        }
    }
    return true;
}

const createButtonContainer = () => {
    const btnContainer = document.createElement("div");
    btnContainer.className = "game-button-container";
    const confirmBtn = document.createElement("button");
    confirmBtn.classList.add("game-button", "confirm-btn");
    confirmBtn.textContent = "Confirm"
    confirmBtn.addEventListener("click", () => {
        if(allPiecesPlaced()) {
            if(!game.vsComputer && playerTurn === 1) {
                playerTurn = 2;
                renderPlacementUI();
            } else {
                boardRenderer.setupGame();
            }
        }
    })

    const resetBtn = document.createElement("button");
    resetBtn.textContent = "Reset";
    resetBtn.classList.add("game-button", "reset-btn");
    resetBtn.addEventListener("click", () => {
        // reset internal board representation
        if(playerTurn === 1) {
            game.player1.gameBoard.reset();
        } else {
            game.player2.gameBoard.reset();
        }

        // reset board UI
        renderBoard()

        // reset placeable pieces
        const pieces = document.querySelectorAll(".piece");
        [...pieces].forEach(piece => {
            piece.classList.remove("dragging");
        })
    })
    btnContainer.appendChild(confirmBtn);
    btnContainer.appendChild(resetBtn);
    return btnContainer;
}

const createErrorContainer = () => {
    const container = document.createElement("div");
    container.className = "error-container";
    const p = document.createElement("p");
    p.className = "error";
    container.appendChild(p);
    return container;
}

const renderPlacementUI = () => {
    const player = playerTurn === 1 ? game.player1 : game.player2;
    
    const bodyContent = document.querySelector(".body-content");
    bodyContent.innerHTML = "";
    const setupContainer = document.createElement("div");
    setupContainer.className = "setup-container";
    const title = document.createElement("h1");
    title.textContent = `Setup ${player.name}'s board`;
    setupContainer.appendChild(title);

    const boardContainer = document.createElement("div");
    boardContainer.className = "game-board";
    setupContainer.appendChild(boardContainer);

    const piecesDiv = document.createElement("div");
    piecesDiv.className = "pieces-div";
    const placeablePiecesContainer = piecesContainer();
    piecesDiv.appendChild(placeablePiecesContainer);
    const changeOrientationBtn = rotateButton();
    piecesDiv.appendChild(changeOrientationBtn);
    setupContainer.appendChild(piecesDiv);

    const buttonContainer  = createButtonContainer();
    setupContainer.appendChild(buttonContainer);

    const errorMsgContainer = createErrorContainer();
    setupContainer.appendChild(errorMsgContainer);

    bodyContent.appendChild(setupContainer);

    renderBoard();
}

const renderUI = () => {
    orientation = "horizontal";
    validPlacement = true;
    playerTurn = 1;
    renderPlacementUI();
}

export default { renderUI }