    --text: #080505;
    --bg: white;
    --primary: #6AC6DE;
    --primary-highlight: rgb(104, 222, 255); 
    --secondary: #8FE6B2;
    --accent: #D43B40;
}

@font-face {
    font-family: Patua;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

body {
    margin: 0px;
    font-family: Patua;
    background-color: var(--bg);
    color: var(--text);
}

button {
    font-family: Patua;
    color: var(--text);
}

header h1 {
    text-align: center;
    margin-top: 50px;
    margin-bottom: 50px;
    font-size: 70px;
    text-shadow: 2px 5px 5px rgb(188, 188, 188);
    letter-spacing: 5px;
}

.button-container {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.body-content {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10%;
}

.game-board {
    --size: 30vh;

    border: solid 1px black;
    height: var(--size);
    width: var(--size);
    display: flex;
    flex-direction: column;
}

form {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

form, 
form input {
    font-size: 30px;
}

form input {
    line-height: 50px;
}

form button {
    margin-top: 50px;
}

.setup-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 100%;
    gap: 20px;
}

.pieces-div {
    display: flex;
    gap: 20px;
    align-items: center;
    width: 50vw;
    height: 20vh;
}

.placeable-pieces-container {   
    width: 60%;
    gap: 20px;
    display: flex;
    flex-direction: column;
}

.row {
    flex: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.piece {
    border: 1px solid black;
    display: flex;
    height: 3vh;
}

.piece-vertical {   
    flex-direction: column;
}

.game-button {
    padding: 10px;
    font-size: 30px;
    border-radius: 15px;
    background-color: white;
    border: 2px solid black;
    box-shadow: 0px 5px black;
    transition: transform 50ms ease-in-out;
}

.menu-button:hover {
    background-color: var(--primary-highlight);
    transform: translate(0px, -2px);
}

.game-button:active {
    transform: translate(0px, 5px);
    box-shadow: none;
}

.confirm-btn {
    background-color: var(--primary-highlight);
}

.reset-btn {
    color: white;
    background-color: var(--accent);
}

.rotate-button {
    box-shadow: 0px 5px black;
    background-color: white;
    transition: transform 50ms ease-in-out;
}

.game-button-container {
    display: flex;
    gap: 50px;
}

.grid-row {
    width: 100%;
    height: 10%;
    display: flex;
}

.player-tile,
.enemy-tile {
    border: 1px solid black;
    width: 100%;
    height: 100%;
}

.selected {
    background-color: rgba(130, 255, 164, 0.72);
    border: 1px solid rgb(130, 255, 164);
}

.invalid-selected { 
    background-color: rgba(255, 130, 130, 0.72);
    border: 1px solid rgb(255, 130, 130);
}

.dragging {
    opacity: 0.3;
    background-color: rgba(0, 0, 0, 0.5);
}

.hoverable-tile:hover {
    border: 1px solid red;
    background-color: rgba(216, 0, 0, 0.3);
}

.hit-tile {
    border: 1px solid rgb(0, 0, 0);
    background-color: rgb(226, 0, 0)
}

.miss-tile {
    border: 1px solid rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.7);
}

.ship-tile {
    background-color: rgba(4, 63, 238, 0.7);
    border: 1px solid rgb(2, 41, 158);
}

.btn-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;

    button {
        border-radius: 10px;
        background-color: rgb(252, 243, 228);
        color: black;
        font-size: 20px;
        padding: 10px;
    }
}

.error {
    color: red;
    font-size: 30px;
}

dialog {
    position: absolute;
    gap: 20px;
    padding: 30px 50px;
    border-radius: 10px;
    width: 20vw;

    div {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;

        p {
            font-size: 30px;
        }

        div {
            display: flex;
            gap: 20px;
            
            button {
                display: inline;
                padding: 5px 10px;
                border-radius: 10px;
            }
        }

        
    }
}

dialog::backdrop {
    background-color: rgba(0, 0, 0, 0.549);
}
