/* eslint-disable no-plusplus */
import ComputerPlayer from "../classes/computerPlayer";
import game from "../classes/game";

const renderPlayerBoard = () => {
    const { turn } = game; 
    const { board } = turn === 1 
        ? game.player1.gameBoard 
        : game.player2.gameBoard;
    const element = turn === 1 
        ? document.querySelector(".player1-board") 
        : document.querySelector(".player2-board");
    element.innerHTML = "";
    board.forEach(arr => {
        const newRow = document.createElement("div");
        newRow.className = "grid-row";
        arr.forEach(tile => {            
            const newTile = document.createElement("div");
            newTile.classList.add("player-tile");
            if(tile === "s") { 
                newTile.classList.add("ship-tile");
            } else if (tile === "x") {
                newTile.classList.add("hit-tile");
            } else if (tile === "o") {
                newTile.classList.add("miss-tile");
            }
            newRow.appendChild(newTile);
        })
        element.appendChild(newRow);
    })
}

const renderEnemyBoard = () => {
    const { turn } = game;
    const { board } = turn === 1 
        ? game.player2.gameBoard 
        : game.player1.gameBoard;
    const element = turn === 1 
        ? document.querySelector(".player2-board") 
        : document.querySelector(".player1-board");
    element.innerHTML = "";
    let i = 0;
    board.forEach(arr => {
        const newRow = document.createElement("div");
        newRow.className = "grid-row";
        let j = 0;
        arr.forEach(tile => {            
            const newTile = document.createElement("div");
            newTile.classList.add("enemy-tile", "hoverable-tile");
            const value = `${j}-${i}`;
            newTile.setAttribute("value", value);
            if(tile === "x") {
                newTile.classList.add("hit-tile");
            } else if (tile === "o") {
                newTile.classList.add("miss-tile");
            }
            newRow.appendChild(newTile);
            j += 1;
        })
        i += 1;
        element.appendChild(newRow);
    })
}

const addEnemyBoardLogic = () => {
    const enemyBoard = game.turn === 1 
        ? document.querySelector(".player2-board") 
        : document.querySelector(".player1-board");
    const rows = Array.from(enemyBoard.children);
    rows.forEach(row => {
        const children = Array.from(row.children);
        children.forEach(child => {
            const [x, y] = child.getAttribute("value").split("-");
            child.addEventListener("click", () => {
                game.attackPlayer(x, y);
                const tile = game.turn === 1 
                    ? game.player2.gameBoard.board[y][x]
                    : game.player1.gameBoard.board[y][x]
                if(tile === "x") {
                    child.classList.add("hit-tile");
                } else if (tile === "o") {
                    child.classList.add("miss-tile");
                }

                if(game.vsComputer) {
                    renderPlayerBoard();
                }                
                child.classList.remove("hoverable-tile");
            }, {once: true})
        })
    })
}

const renderBoards = () => {
    renderPlayerBoard();
    renderEnemyBoard();
    addEnemyBoardLogic();
}

const setupGame = () => {
    const player1Container = document.createElement("div");
    const player1Title = document.createElement("h1");
    player1Title.textContent = game.player1.name;
    player1Container.appendChild(player1Title);
    const player1Board = document.createElement("div");
    player1Board.classList.add("player1-board", "game-board");
    player1Container.appendChild(player1Board);
    
    const player2Container = document.createElement("div");
    const player2Title = document.createElement("h1");
    if(game.player2 instanceof ComputerPlayer) {
        player2Title.textContent = "Computer"
    } else {
        player2Title.textContent = game.player2.name;
    }
    player2Container.appendChild(player2Title);
    const player2Board = document.createElement("div");
    player2Board.classList.add("player2-board", "game-board");
    player2Container.appendChild(player2Board);
    
    const bodyContent = document.querySelector(".body-content");
    bodyContent.innerHTML = "";
    bodyContent.appendChild(player1Container);
    bodyContent.appendChild(player2Container);
    renderBoards();
}

export default { renderBoards, setupGame }