import game from "../classes/game";
import setupRenderer from "./setupRenderer";
import shipPlacementUIRenderer from "./shipPlacementUIRenderer";

const setupPlayAgainBtn = () => {
    const resetBtn = document.querySelector(".play-again-btn");
    resetBtn.addEventListener("click", () => {
        const modal = document.querySelector("dialog");
        modal.close();
        game.reset();
        shipPlacementUIRenderer.renderUI();
    })
}

const setupResetBtn = () => {
    const playAgainBtn = document.querySelector(".reset-btn");
    playAgainBtn.addEventListener("click", () => {
        const modal = document.querySelector("dialog");
        modal.close();
        setupRenderer.setup();
    })
}

const showGameOver = () => {
    setupResetBtn();
    setupPlayAgainBtn();
    const gameOverModal = document.querySelector("dialog");
    const title = document.querySelector("dialog div h1");
    title.innerHTML = `${game.winner} wins`;
    gameOverModal.showModal();
}

const update = () => {
    showGameOver();
}

export default { showGameOver, setupResetBtn, update };