import game from "../classes/game";
import shipPlacementUIRenderer from "./shipPlacementUIRenderer";

const setupGame = () => {
    const player1Element = document.querySelector("form input[name='player1Name']");
    const player2Element = document.querySelector("form input[name='player2Name']");
    const player1Name = player1Element.value;
    const player2Name = player2Element === null ? null : player2Element.value;
    game.setupPlayers(player1Name, player2Name);
    shipPlacementUIRenderer.renderUI();
}

const onePlayerSetup = () => {
    const bodyContent = document.querySelector(".body-content");
    bodyContent.innerHTML = `
        <form action="">
            <label for="player1Name">Player Name</label>
            <input type="text" name="player1Name" id="player1Name" required>
            <button class="menu-button game-button" type="submit">Play</button>
        </form>
    `;    
    const playBtn = document.querySelector("form button");
    playBtn.addEventListener("click", () => {
        const player1Name = document.querySelector("#player1Name").value;
        if(player1Name) {
            setupGame();
        }
    });
}

