export default class Ship {
    constructor(length) {
        this.length = length;
        this.hits = 0;
        this.sunk = false;
    }

    reset() {
        this.hits = 0;
    }

    hit() {
        this.hits += 1;
        return this.isSunk();
    }

    isSunk() {
        if(this.hits === this.length) {
            this.sunk = true;
            return true;
        }
        return false;
    }
}



