import ComputerPlayer from "./computerPlayer";
import Player from "./player";

class Game {
    constructor() {
        this.player1 = null;
        this.player2 = null;
        this.vsComputer = false;
        this.subscribers = [];
        this.initialise();
    }

    addSub(obj) {
        this.subscribers.push(obj);
    }

    updateSubs() {
        this.subscribers.forEach(sub => {
            sub.update();
        });
    }

    attackPlayer(x, y) {
        const hit = this.turn === 1 ? this.player2.receiveAttack(x, y) : this.player1.receiveAttack(x, y);
        if(hit) {
            const allSunk = this.turn === 1 ? this.player2.isAlive() : this.player1.isAlive();
            if(allSunk) {
                this.winner = this.turn === 1 ? this.player1.name : this.player2.name;
                this.over = true;
                this.updateSubs();
            } else if (this.vsComputer && this.turn === 2) {
                this.computerTurn();
            }
        } else if (this.vsComputer && this.turn === 1) {
            this.changeTurn();
            this.computerTurn();
        } else {
            this.changeTurn();
        }
    }

    changeTurn() {
        this.turn = this.turn === 1 ? 2 : 1;
    }

    computerTurn() {
        const computerMove = this.player2.getMove()
        this.attackPlayer(computerMove[0], computerMove[1]);
    }

    get isGameOver() {
        return this.over;
    }

    placeShip(size, direction, x, y) {
        if(this.turn === 1) {
            this.player1.gameBoard.placeShip(size, direction, x, y);
        } else if (this.turn === 2 && !this.vsComputer) {
            this.player2.gameBoard.placeShip(size, direction, x, y);
        }
    }

    setupPlayers(player1Name, player2Name) {
        this.player1 = new Player(player1Name);
        this.player2 = player2Name === null ? new ComputerPlayer() : new Player(player2Name);   
        if(this.player2 instanceof ComputerPlayer) {
            this.vsComputer = true;
            this.computerSetup();
            
        }
    }

    computerSetup() {
        const shipsLengths = [1, 1, 1, 1, 2, 2, 2, 3, 3, 4];
        shipsLengths.forEach(length => {
            let valid = false;
            while(!valid) {
                const directionOptions = ["down", "left"];
                const direction = directionOptions[Math.floor(Math.random())];
                const x = Math.floor(Math.random() * 9);
                const y = Math.floor(Math.random() * 9);
                valid = this.player2.gameBoard.placeShip(length, direction, x, y);
            }
        })
    }

    reset() {
        this.player1.gameBoard.reset();
        this.player2.gameBoard.reset();
        this.initialise();
        this.computerSetup();
    }

    initialise() {
        this.winner = null;
        this.turn = 1;
        this.over = false;
    }
}

const game = new Game();

export default game;
